.art-item {
    margin-bottom: 1.5rem;
    @include q(900) {
        margin-bottom: 3rem; }
    &__img {
        margin-bottom: 1.5rem; }
    &__info {
        margin-bottom: 1.5rem;
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        .date, .view {
            flex-basis: 50%;
            @include q(400) {
                flex-basis: auto;
                margin-right: 15px; } }
        .tag {
            flex-basis: 100%;
            margin-right: 2rem;
            margin-bottom: 1rem;
            @include q(400) {
                flex-basis: auto; }
            @include q(600) {
                margin-bottom: 0; } } }
    &__body {
        margin-bottom: 1.5rem; } }



.art-item__body {
    p {
        margin: 1rem 0;
        color: #333; }
    ul li {
        list-style-type: none;
        position: relative; }
    ul li:before {
        position: absolute;
        content: '';
        width: 10px;
        height: 10px;
        left: -17px;
        top: 6px;
        margin: auto;
        border-radius: 50%;
        background-color: $yellow; }
    blockquote {
        position: relative;
        background-color: $gray-5;
        margin: 0 20px;
        padding: 16px 13px; }
    blockquote:after {
        position: absolute;
        content: '';
        left: 1px;
        top: 0;
        bottom: 0;
        width: 4px;
        background-color: $yellow; } }




.related-posts {
    background-color: $gray-1;
    padding: 1.5rem 0;
    @include q(900) {
        padding: 3.75rem 0; }
    &__title {
        margin: 0;
        margin-bottom: 1.5rem; }
    &__wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between; }
    &__item {
        width: 100%;
        @include q(900) {
            width: 30%; } } }

.article--related {
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
    flex-direction: column;
    .btn {
        width: 100% !important; }
    .article__link {
        padding-bottom: 63%; }
    .article__title {
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden; }
    .article__text {
        -webkit-line-clamp: 2; } }

.slider {
    margin-bottom: 2rem;
    &__item {
        margin-bottom: .5rem;
        @include q(900) {
            margin-bottom: 1rem; } } }

.slider-nav {
    .slick-slide {
        font-size: 0;
        .slider-nav__item {
            cursor: pointer;
            padding: 2%; } }
    .slick-slide.slick-current {
        position: relative; }
    .slick-slide.slick-current:after {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: transparent;
        border: 2px solid $yellow; } }
