.article__info { order: -1; width: 100%; margin-bottom: 1rem; display: flex; justify-content: space-around; }

@media only screen and (min-width: 600px) { .article__info { margin-left: auto; align-self: center; width: auto; order: 1; align-self: center; margin-bottom: 0; } }

.article__content-item { margin-bottom: 1rem; }

.article__content-item:last-child { margin-bottom: 0; }

.article .btn { padding-left: 3rem; padding-right: 3rem; width: 100%; }

@media only screen and (min-width: 600px) { .article .btn { width: auto; } }

.article:hover { box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.3); }

.tags { display: flex; flex-wrap: wrap; justify-content: flex-start; align-items: center; margin-bottom: 1.75rem; }

@media only screen and (min-width: 900px) { .tags { margin-bottom: 3.75rem; } }

.tags__item { margin-right: 20px; margin-bottom: 10px; }

.tag { color: #fff; text-transform: uppercase; font-size: .875rem; padding: .5em 1.75em; display: inline-block; border: 2px solid #faac09; color: #333; font-weight: 700; }

.tag--active { background-color: #faac09; text-decoration: none; color: #fff; }

.tag--lg { padding: .625rem 1.75rem; }

.tag:hover { text-decoration: none; }

.date, .view { padding-left: 35px; background-size: 15px; }

.date { background: transparent url("../img/date.svg") 10px center no-repeat; }

.view { background: transparent url("../img/view.svg") 5px center no-repeat; }

.modal { position: fixed; top: 0; right: 0; bottom: 0; left: 0; z-index: 1050; display: none; overflow: hidden; outline: 0; z-index: 1070; }

.modal-overlay { position: fixed; top: 0; right: 0; bottom: 0; left: 0; z-index: 1040; background-color: #000; opacity: .75; }

.modal-dialog { max-width: 465px; min-height: calc(100% - (2rem * 2)); display: flex; align-items: center; margin: 2rem auto; z-index: 1070; position: relative; }

.modal-dialog .btn--close { position: absolute; right: 5px; top: 5px; transform: translate(0, 0); width: 30px; height: 30px; }

@media only screen and (min-width: 600px) { .modal-dialog .btn--close { transform: translate(100%, -100%); width: 40px; height: 40px; } }

.modal-dialog .btn--close:after, .modal-dialog .btn--close:before { background-color: #333; }

@media only screen and (min-width: 600px) { .modal-dialog .btn--close:after, .modal-dialog .btn--close:before { background-color: #efefef; } }

.modal-content { background-color: #fff; box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.1); padding: 1rem; position: relative; margin: auto; }

@media only screen and (min-width: 900px) { .modal-content { padding: 3.25rem; } }

.modal-header { text-align: center; margin-bottom: 2rem; }

.modal-header__title { display: block; font-size: 1.5rem; font-weight: bold; text-align: center; text-transform: uppercase; }

@media only screen and (min-width: 900px) { .modal-header__title { font-size: 2.5rem; } }

.modal-header__text { font-size: 1rem; display: inline-block; }

.modal-footer { margin-top: 3rem; }

.modal-open { overflow: hidden; }

.modal-open .modal { overflow-x: hidden; overflow-y: auto; }

.modal-body { position: relative; }

.invalid-feedback { position: absolute; bottom: 0; left: 0; font-weight: bold; font-size: .875rem; color: #faac09; transform: translateY(110%); display: none; }

.modal input[type="email"].not-valid ~ .invalid-feedback { display: block; }

.input-wrap { display: flex; flex-wrap: wrap; width: 100%; align-items: stretch; margin-bottom: 1.25rem; }

.form-input { display: block; width: 100%; padding: .5rem 1rem; font-size: 1rem; line-height: 1.5; color: #333; background-color: #fff; background-clip: padding-box; border: 2px solid #efefef; border-radius: 0; transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out; font-family: "Roboto", Sans-Serif; }

.form-input::placeholder { font-size: .875rem; font-weight: light; color: #b0b0b0; }

textarea { resize: none; min-height: 130px; }

.dropdown { position: relative; }

.dropdown__menu { position: absolute; top: 100%; left: 0; right: 0; z-index: 1000; display: none; font-size: 1rem; color: #fff; font-weight: bold; text-align: left; list-style: none; background-color: #333; background-clip: padding-box; }

.dropdown__item { display: block; width: 100%; text-align: center; white-space: nowrap; padding: .625rem 1.25rem; }

.dropdown__toggle { padding: .625rem 1.25rem; border: none; position: relative; outline: none; }

.dropdown__toggle:after { position: absolute; content: ''; width: 6px; height: 6px; border-bottom: 2px solid #fff; border-right: 2px solid #fff; transform-origin: center top; transform: rotate(45deg); top: 0; right: 5px; bottom: 0; margin: auto; }

.dropdown__item.dropdown__item--disabled { background-color: #4d4d4d; color: #787878; pointer-events: none; }

.art-item { margin-bottom: 1.5rem; }

@media only screen and (min-width: 900px) { .art-item { margin-bottom: 3rem; } }

.art-item__img { margin-bottom: 1.5rem; }

.art-item__info { margin-bottom: 1.5rem; align-items: center; display: flex; flex-wrap: wrap; }

.art-item__info .date, .art-item__info .view { flex-basis: 50%; }

@media only screen and (min-width: 400px) { .art-item__info .date, .art-item__info .view { flex-basis: auto; margin-right: 15px; } }

.art-item__info .tag { flex-basis: 100%; margin-right: 2rem; margin-bottom: 1rem; }

@media only screen and (min-width: 400px) { .art-item__info .tag { flex-basis: auto; } }

@media only screen and (min-width: 600px) { .art-item__info .tag { margin-bottom: 0; } }

.art-item__body { margin-bottom: 1.5rem; }

.art-item__body p { margin: 1rem 0; color: #333; }

.art-item__body ul li { list-style-type: none; position: relative; }

.art-item__body ul li:before { position: absolute; content: ''; width: 10px; height: 10px; left: -17px; top: 6px; margin: auto; border-radius: 50%; background-color: #faac09; }

.art-item__body blockquote { position: relative; background-color: #f6f6f6; margin: 0 20px; padding: 16px 13px; }

.art-item__body blockquote:after { position: absolute; content: ''; left: 1px; top: 0; bottom: 0; width: 4px; background-color: #faac09; }

.related-posts { background-color: #f1f1f1; padding: 1.5rem 0; }

@media only screen and (min-width: 900px) { .related-posts { padding: 3.75rem 0; } }

.related-posts__title { margin: 0; margin-bottom: 1.5rem; }

.related-posts__wrap { display: flex; flex-wrap: wrap; justify-content: space-between; }

.related-posts__item { width: 100%; }

@media only screen and (min-width: 900px) { .related-posts__item { width: 30%; } }

.article--related { max-width: 400px; margin-left: auto; margin-right: auto; flex-direction: column; }

.article--related .btn { width: 100% !important; }

.article--related .article__link { padding-bottom: 63%; }

.article--related .article__title { display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical; overflow: hidden; }

.article--related .article__text { -webkit-line-clamp: 2; }

.slider { margin-bottom: 2rem; }

.slider__item { margin-bottom: .5rem; }

@media only screen and (min-width: 900px) { .slider__item { margin-bottom: 1rem; } }

.slider-nav .slick-slide { font-size: 0; }

.slider-nav .slick-slide .slider-nav__item { cursor: pointer; padding: 2%; }

.slider-nav .slick-slide.slick-current { position: relative; }

.slider-nav .slick-slide.slick-current:after { position: absolute; content: ''; top: 0; left: 0; right: 0; bottom: 0; background-color: transparent; border: 2px solid #faac09; }

.breadcrumb { display: flex; margin: 0; padding: 0; list-style: none; }

.breadcrumb__item { border-right: 2px solid #333; margin-right: .625rem; padding-right: .625rem; margin-bottom: 2rem; }

.breadcrumb__item:last-child { border-right: none; }

.breadcrumb__item:last-child .breadcrumb__link { font-weight: 700; text-decoration: none; }

.breadcrumb__link { color: #333; }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }
