.grid {
    min-height: 100vh; }

.grid-item {
    will-change: left, top, transfrom, opacity; }

.progress-bar {
    margin-bottom: 1.75rem;
    overflow: hidden;
    @include q(900) {
        overflow: visible;
        margin-bottom: 3.75rem; }
    &__btns {
        display: flex;
        justify-content: space-between; }
    &__row {
        padding: 50px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        transition: transform 1s cubic-bezier(0.42, 0, 0, 1.82);
        will-change: transform;
        @include qmax(899) {
            padding: 50px 10px; } }
    &__row:before {
        position: absolute;
        content: '';
        height: 0;
        background-color: $yellow;
        border: 4px solid $yellow;
        top: 0;
        bottom: 0;
        left: 10px;
        right: 20px;
        margin: auto;
        z-index: -2; }
    &__item {
        display: flex;
        flex: 1;
        position: relative;
        @include qmax(899) {
            pointer-events: none; } }
    &__item:last-child {
        flex: 0; }
    &__item.is-active:after {
        position: absolute;
        content: '';
        background-color: $gray-5;
        top: 0;
        bottom: 0;
        left: 0;
        height: 0;
        margin: auto;
        width: 100%;
        border-top: 5px solid $gray-5;
        border-bottom: 5px solid $gray-5;
        z-index: -2; }
    &__item.is-active ~ .progress-bar__item:after {
        position: absolute;
        content: '';
        background-color: $gray-5;
        top: 0;
        bottom: 0;
        left: 0;
        height: 0;
        margin: auto;
        width: 100%;
        border-top: 5px solid $gray-5;
        border-bottom: 5px solid $gray-5;
        z-index: -2; }
    &__item.is-active ~ .progress-bar__item .progress-bar__btn {
        background-color: $gray-6;
        border-color: $gray-6; }
    &__item.is-active ~ .progress-bar__item .progress-bar__btn:after {
        border-color: $gray-6; }
    &__item:last-child:after {
        display: none; }
    &__item:last-child {
        .progress-bar__event, .progress-bar__date {
            right: 0;
            left: auto;
            @include qmax(899) {
                transform: translateX(calc(50% - 12px)); } } }
    &__btn {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        background-color: $bg-dark;
        position: relative;
        left: 0px;
        border: 2px solid $bg-dark; }
    &__btn:after {
        position: absolute;
        content: '';
        border: 2px solid $bg-dark;
        left: -50%;
        top: -50%;
        margin: auto;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        transform: scale(0);
        transition: transform .3s cubic-bezier(0.09, 0.26, 0.52, 1.95); }
    &__btn.is-checked:after {
        transform: scale(1); }
    &__event {
        position: absolute;
        left: 0;
        bottom: 150%;
        $color: $base-font-color;
        font-size: .875rem;
        font-weight: bold;
        max-width: 90px;
        transition: opacity .8s;
        @include qmax(899) {
            opacity: 0;
            transform: translateX(calc(-50% + 12px)); } }
    &__date {
        position: absolute;
        left: 0;
        top: 150%;
        font-weight: bold;
        font-size: 0.875rem;
        color: $disable-color;
        transition: opacity .8s;
        @include qmax(899) {
            opacity: 0;
            transform: translateX(calc(-50% + 12px)); } }
    &__btn.is-checked ~ .progress-bar__event {
        opacity: 1; }
    &__btn.is-checked ~ .progress-bar__date {
        opacity: 1; } }

.roadmap .btn.prev,
.roadmap .btn.next {
    @include q(900) {
        display: none; } }


.grid-item {
    margin-bottom: 30px; }

.grid-item,
.grid-sizer {
    width: 100%;
    @include q(600) {
        width: 48%; }
    @include q(1000) {
        width: 32%; } }

.gutter-sizer {
    width: 2%; }



.card {
    background-color: #fff;
    box-shadow: 0px 3px 6px 0px rgba(0, 11, 10, 0.05);
    transition: box-shadow .3s ease-in-out;
    &__header {
        padding-bottom: 1rem;
        padding-top: 2rem; }
    &__header-text {
        display: inline-block;
        width: 72%;
        padding: 0 1.875rem;
        background-color: #faac03;
        color: #fff;
        font-size: .875rem;
        font-weight: 700;
        text-transform: uppercase; }
    &__body {
        padding: 1.5rem 1.875rem;
        padding-top: 0; }
    &__body-title {
        color: #333333;
        font-weight: 700;
        font-size: 1.375rem;
        text-transform: uppercase;
        margin-bottom: 1rem; }
    &__body-text {
        color: #868686;
        font-weight: 300;
        font-size: 1rem; } }

.card:hover {
    box-shadow: 0 3px 6px 0 rgba(0,0,0,.3); }

.grid-item.done {
    .card__header-text {
        background-color: #aaa; } }
